<template>
  <div class="params" id="dropImg">
    <el-breadcrumb separator-class="el-icon-arrow-right" v-if="type === 2">
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item class="attrbute">
        <span @click="goBack"></span>
      </el-breadcrumb-item>
      <el-breadcrumb-item>{{ editType == 2 ? "" : "" }}</el-breadcrumb-item>
    </el-breadcrumb>
    <div id="leixin">
      <div class="search_container">
        <!--  -->
        <el-form :inline="true" :model="params" class="demo-form-inline" ref="ruleForm" :rules="rules">
          <el-form-item label="" size="mini" prop="company_id" :rules="rules.company_id">
            <getServiceCompany v-model="params.company_id" ref="getServiceCompany" :searchParams="{ is_service: 2 }" :fatherParams="params" :disabled="editType == 2" @getList="getCompany" />
          </el-form-item>
          <el-form-item label="" size="mini" prop="customer_id" :rules="rules.customer_id" style="width: 400px;">
            <getServiceUser v-model="params.customer_id" :fatherParams="params" :disabled="editType == 2" />
            <el-button @click="NewCustomer()" style="width: auto;" size="mini" type="primary"></el-button>
          </el-form-item>
          <!-- <el-form-item>
            <el-button @click="NewCustomer()" size="mini" type="primary"></el-button>
          </el-form-item> -->
          <template v-if="editType == 2">
            <el-form-item label="" size="mini">
              {{ params.auditor_status }}
            </el-form-item>
            <el-form-item label="" size="mini">
              {{ params.add_status }}
            </el-form-item>
            <el-form-item label="" size="mini">
              {{ params.outstock_sn }}
            </el-form-item>
            <el-form-item label="" size="mini">
              {{ params.add_time }}
            </el-form-item>
          </template>
        </el-form>
      </div>
      <searchGoods v-if="editType == 1" @addGoods="addGoods" :tableBox="tableData" @openHistoryPrice="openHistoryPrice" :searchParams="{
          company_id: params.company_id,
          customer_id: params.customer_id
        }"></searchGoods>
      <div class="goodsList">
        <div class="goodsTitle">
          
        </div>
      </div>
      <div>
        <xTable :maxHeight="200" ref="xTable" :edit-rules="editRules" rowId="goodsLists" :tableColumn="tableColumn" :xData="tableData" :footerMethod="footerMethod" />
      </div>
      <div class="costDetail">
        <el-row :gutter="5">
          <el-col :span="12">
            <span class="costDetailTitle">
              
            </span>
            <el-radio v-model="params.discount_type" :label="0" @change="computedTotalPrice"></el-radio>
            <el-radio v-model="params.discount_type" :label="1" @change="computedTotalPrice"></el-radio>
            <el-input v-model="params.discount_num" class="discountInput" @input="computedTotalPrice" @change="
                $publicFution.toFixedTwo(params, 'discount_num', {
                  greater: 0,
                  max: 10
                });
                computedTotalPrice();
              ">
              <span slot="suffix" class="el-input__icon"></span>
            </el-input>
            <el-radio v-model="params.discount_type" :label="2" @change="computedTotalPrice"></el-radio>
            <el-input v-model="params.discount_total_amount" class="discountInput" @input="computedTotalPrice" @change="discountTotalChange">
              <span slot="suffix" class="el-input__icon"></span>
            </el-input>
          </el-col>
          <el-col :span="12">
            <span class="costDetailTitle">
              
            </span>
            
            <el-input v-model="params.service_charge" class="discountInput" @input="computedTotalPrice" @change="
                $publicFution.toFixedTwo(params, 'service_charge', { min: 0 });
                computedTotalPrice();
              "></el-input>
            
            <el-input v-model="params.packing_fee" class="discountInput" @input="computedTotalPrice" @change="
                $publicFution.toFixedTwo(params, 'packing_fee', { min: 0 });
                computedTotalPrice();
              "></el-input>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <span class="costDetailTitle"></span>
            <el-input v-model="params.pay_price" class="discountInput" @change="payPriceChange" @input="computedTotalPrice"></el-input>
            <span>  {{ params.arrears }} </span>
          </el-col>
          <el-col :span="12">
            
            <el-input type="text" v-model="params.abstract" style="width:80%;" maxlength="200" show-word-limit></el-input>
          </el-col>
        </el-row>
        <div>
          <span class="costDetailTitle"> </span>
          <el-select v-model="params.delivery_demand_id" placeholder="">
            <el-option v-for="(val, key) in deliveryDemandList" :key="key" :label="val.delivery_demand_name" :value="val.delivery_demand_id"></el-option>
          </el-select>
        </div>
        <div>
          <span class="totalPrice">
             <span style="color:red;">{{ params.settlement_price }}</span>
          </span>
        </div>
      </div>
    </div>
    <div class="dialog-footer">
      <el-button v-if="type == 2" @click="goBack"></el-button>
      <el-button v-if="authority.b_add && authority.b_print" type="primary" @click="submit(1)"></el-button>
      <el-button v-if="authority.b_add" type="primary" @click="submit(2)"></el-button>
    </div>
    <historyPrices v-if="isShowHistoryPrices" v-model="isShowHistoryPrices" @sumbitData="changeRowPrice" :searchParams="{
        company_id: params.company_id,
        goods_id: currentScope.row && currentScope.row.goods_id
      }" :company_id="params.company_id" :row="currentScope" />
    <lodopDelivery ref="lodopDelivery" title="serviceOutbound"></lodopDelivery>
  </div>
</template>
<script>
import { tableHeader, paramsModel } from "./config/salesOpenOrder";
import historyPrices from "common/components/plugin/service/historyPrices";
import { getLodop, setLoadFinish } from "common/utils/LodopFuncs.js";
import lodopDelivery from "common/components/public/PublicPrint.vue"; //
import searchGoods from "../config/searchGoods";
export default {
  name: "salesOpenOrder",
  mixins: [tableHeader],
  components: {
    historyPrices,
    searchGoods,
    lodopDelivery,
  },
  props: {
    type: Number,
    default: 1, // 1  2
  },
  data() {
    return {
      tableData: [],
      isMultiple: false,
      currentScope: {},
      isShowHistoryPrices: false,
      params: this.$publicFution.deepCopy(paramsModel),
      deliveryDemandList: [],
      editType: 1, // 
    };
  },
  methods: {
    discountTotalChange() {
      if (this.params.discount_total_amount < 0) {
        this.$message.warning("0");
        this.params.discount_total_amount = "";
      }
      if (
        Number(this.params.discount_total_amount) >
        Number(this.params.goodsAllprice)
      ) {
        this.$message.warning("");
        this.params.discount_total_amount = "";
      }
      this.computedTotalPrice();
    },
    //
    NewCustomer() {
      this.$router.push({
        path: "/custom/service_station_list/service_station_list_add",
      });
    },
    payPriceChange() {
      if (this.params.pay_price < 0) {
        this.$message.warning("0");
        this.params.pay_price = "";
      }
      if (
        Number(this.params.pay_price) > Number(this.params.settlement_price)
      ) {
        this.$message.warning("");
        this.params.pay_price = "";
      }
      this.computedTotalPrice();
    },
    // 
    getCompany(list) {
      this.isMultiple = list.length > 1 ? true : false;
    },
    // 
    getDeliveryDemandList(state) {
      this.$api
        .serviceStationGetDeliveryDemandList({
          is_service: 1, // 
        })
        .then(({ data }) => {
          if (data.code == 200) {
            this.deliveryDemandList = data.data;
            this.deliveryDemandList.forEach((val) => {
              if (val.delivery_demand_name == "" && !state) {
                this.params.delivery_demand_id = val.delivery_demand_id;
              }
            });
          }
        });
    },
    submit(type) {
      var tableData = this.$refs.xTable
        ._proxy("getTableData")
        .fullData.concat([]);
      if (
        tableData[tableData.length - 1] &&
        !tableData[tableData.length - 1].goods_id
      ) {
        tableData.pop();
      }
      if (tableData.length < 1) {
        this.$message.warning("");
        return;
      }
      this.$refs.ruleForm.validate((valid) => {
        if (valid) {
          this.$refs.xTable._proxy("validate", tableData).then((errMap) => {
            var params = this.$publicFution.deepCopy(this.params);
            tableData = tableData.map((val, key) => {
              val.serial_number = key;
              val.business_type_id = 10001;
              return val;
            });
            params.goods_info = JSON.stringify(tableData);
            let url = "serviceStationAddSalesOut";
            if (this.editType === 2) {
              params.outstock_id = this.$route.query.id;
              url = "serviceStationAuidtorPassSalesOut";
            }
            this.$api[url](params).then(({ data }) => {
              if (data.code === 200) {
                this.$message.success(data.msg);
                if (type == 1) {
                  this.$refs.lodopDelivery.printPdf(
                    {
                      outstock_id: data.data,
                    },
                    0,
                    this.goBack
                  );
                } else {
                  this.goBack();
                }
              }
            });
          });
        }
      });
    },
    goBack() {
      this.$publicFution.goPage(this.$route.query.id);
      this.$router.push("/sales/service_station_sales_outstock_list");
    },
  },
  created() {
    if (this.$route.query.id) {
      this.editType = 2;
      this.$api
        .serviceStationGetDeliveryGetSalesOutInfo({
          outstock_id: this.$route.query.id,
        })
        .then(({ data }) => {
          if (data.code == 200) {
            this.params = Object.assign(paramsModel, data.data);
            if (this.params.delivery_demand_id == 0) {
              this.getDeliveryDemandList();
            }
            this.params.arrears = (
              this.params.settlement_price -
              this.getNumber(this.params.pay_price)
            ).toFixed(2); // 
            this.$refs.xTable._proxy("loadData", data.data.goods_list);
          }
        });
    }
    this.getDeliveryDemandList();
    if (window.CLodopLoadFinComplete) {
      getLodop();
    } else {
      setLoadFinish(() => {
        getLodop();
      });
    }
  },
};
</script>

<style scoped lang="less">
.goodsList {
  margin: 5px;
  .goodsTitle {
    font-weight: bold;
  }
  .goodsInfo {
    > div {
      float: right;
    }
    &::after {
      content: "";
      display: block;
      clear: both;
    }
  }
}
.costDetail {
  font-size: 14px;
  line-height: 35px;
  color: rgba(0, 0, 0, 0.5);
  .discountInput {
    width: 100px;
  }
  .costDetailTitle {
    font-weight: bold;
  }
  /deep/ .el-textarea__inner {
    vertical-align: text-top;
    width: 450px;
  }
  .totalPrice {
    font-size: 20px;
    font-weight: bold;
    color: black;
  }
  /deep/ .el-radio {
    margin-right: 10px !important;
  }
}
</style>
