<template>
  <div class="params" id="dropImg">
    <div class="search_container">
      <el-form :inline="true" :model="params" class="demo-form-inline" ref="ruleForm" :rules="rules">
        <div style="margin-top: 2px;">
          <el-form-item label="" size="mini" prop="oe_code" style="width: 620px;">
            <el-input style="width: 388px;" v-model="params.goods_text" maxlength="100" placeholder="/OE" clearable @keyup.enter.native="onSearch()"></el-input>
            <el-button @click="onSearch()" size="mini" type="primary"></el-button>
            <el-button plain @click="onReset" size="mini"></el-button>
          </el-form-item>
          <el-form-item label="VIN:" size="mini" style="width: 500px;">
            <vinCode v-model="params.vin" ref="vinCode" @search="vinSearch" />
            <el-button type="primary" @click="vinSearch"></el-button>
            <el-button style="width: auto;" type="primary" size="mini" :class="!showModelIsLoad ? 'is-disabled' : ''" @click="openSelectModel"></el-button>
          </el-form-item>
        </div>
      </el-form>
    </div>
    <div style="margin-left: 50px;">
      <Classification :searchParams="params" :type="2" @search="searchChange" ref="Classification"></Classification>
    </div>
    <div class="goodsList">
      <div class="goodsTitle">
        
      </div>
      <div class="goodsInfo">
        <getServiceBatch v-model="params.batch" :searchParams="params" @change="batchChange" />
        <getServiceAllocation v-model="params.allocation_id" :searchParams="params" @change="onSearch" />
        <getServiceWarehouse v-model="params.warehouse_id" :searchParams="params" @change="warehouseChange" />
      </div>
    </div>
    <div>
      <xTable ref="xTableTwo" :maxHeight="200" rowId="goodsList" :sign="{ str: 'batch_id', arr: tableBox }" :tableColumn="tableColumn" @getData="dealData" :ajax="{ url: 'serviceStationGetSalesOutGoodsList', params: params }" />
    </div>
    <modelDetail v-model="isShowModelDetail" :type="2" :library_id="currentCarIds" />
    <selectModel v-model="isShowModel" ref="selectModel" @sumbitData="carModelChange" @load="showModelIsLoad = true" />
  </div>
</template>

<script>
import { tableHeader, paramsModel } from "./config/searchGoods";
import Classification from "common/components/plugin/service/goodsTypeCar/Classification.vue";
import selectModel from "common/components/plugin/service/selectModel.vue";
import vinCode from "common/components/plugin/service/vinCode.vue";
import modelDetail from "common/components/plugin/service/modelDetail.vue";

export default {
  mixins: [tableHeader],
  data() {
    return {
      isShowModel: false,
      currentCarIds: [],
      isShowModelDetail: false,
      isShowCarTypes: false,
      showModelIsLoad: false,
      params: this.$publicFution.deepCopy(paramsModel),
    };
  },
  props: {
    searchParams: {
      type: Object,
      default: () => {},
    },
    tableBox: {
      type: Object,
      default: () => [],
    },
  },
  components: {
    selectModel,
    vinCode,
    Classification,
    modelDetail,
  },
  created() {
    this.params = Object.assign(this.params, this.searchParams);
  },
  methods: {
    vinSearch() {
      //
      var vin = this.params.vin;
      var company_id = this.params.company_id;
      this.$refs.Classification.clear();
      this.params = Object.assign({}, { vin: vin, company_id: company_id });
      this.getFairModel();
      this.$refs.vinCode._close();
    },
    searchChange(obj) {
      if (obj.carModel) {
        //table
        delete this.params.car_lib_id;
        let car_lib_name = obj.carModel.car_list.map((item) => {
          return item.sales_name;
        });
        this.params.car_lib_name = car_lib_name.join(",");
      }
      this.onSearch();
    },
    onSearch(num) {
      this.params = Object.assign(this.params, this.searchParams);
      // this.params.vin = "";
      this.$refs.xTableTwo._getData(num);
      this.$refs.vinCode._close();
    },
    onReset() {
      this.params = this.$publicFution.deepCopy(paramsModel);
      this.onSearch(1);
      this.$refs.Classification.clear();
    },
    // 
    batchChange() {
      this.params.allocation_id = "";
      this.onSearch();
    },
    // 
    warehouseChange() {
      this.params.allocation_id = "";
      this.params.batch = "";
      this.onSearch();
    },
    // 
    seeCarModels(row) {
      this.currentCarIds = row.library_id;
      this.isShowModelDetail = true;
    },
    // 
    openSelectModel() {
      if (!this.showModelIsLoad) {
        this.$message.warning("");
        return;
      }
      this.isShowModel = true;
    },
    // 
    carModelChange(val) {
      this.$refs.Classification._addCarModel(val);
      this.isShowModel = false;
    },
    // 
    getFairModel() {
      //
      if (this.params.vin) {
        this.$axMethod
          .getCarLibraryByVin({ vin: this.params.vin })
          .then(({ data }) => {
            if (data.code == 200) {
              this.$refs.Classification.carModelBox = data.data;
              if (data.data.length) {
                this.$refs.Classification.carModelChange(data.data[0]); //
              }
            }
          });
      }
    },
    dealData({ data }) {
      this.$refs.Classification.echoSearch(data);
    },
  },
};
</script>

<style scoped lang="less">
.goodsList {
  margin: 5px;
  .goodsTitle {
    font-weight: bold;
  }
  .goodsInfo {
    > div {
      float: right;
    }
    &::after {
      content: "";
      display: block;
      clear: both;
    }
  }
}
</style>
