<template>
  <div class="params">
    <vxe-modal v-model="value" title='' @hide="close" width="1200" :esc-closable='true' destroy-on-close height="600" min-width="600" min-height="100" resize remember storage transfer>
      <div>
        <el-form :inline="true" :model="params" class="demo-form-inline">

          <el-form-item label="" size="mini">
            <erpUsers v-model="params.customer_id" :searchParams='{ company_id: company_id }' />
          </el-form-item>
          <el-form-item size="mini">
            <el-button @click="onSearch" size="mini" type="primary" style="margin-left:50px;"></el-button>
            <el-button plain @click="onReset" size="mini"></el-button>
          </el-form-item>
          <br>
          <el-form-item label="" size="mini">
            {{ row.row.goods_name }}
          </el-form-item>
          <el-form-item label="" size="mini">
            {{ showData.lately_price }}
          </el-form-item>
          <el-form-item label="" size="mini">
            {{ showData.average_price }}
          </el-form-item>
        </el-form>

        <xTable @cellDblclick='submit' rowId='historicalPrices' @getData='dealData' @current-change='currentChange' :ajax='{ url: "getHistoryOrder", params: params }' ref='xTable' :tableColumn='tableColumns' maxHeight='350px' />
      </div>
      <div class="dialog-footer">
        <el-button type="primary" @click="submit()"></el-button>
        <el-button @click="close"> </el-button>
      </div>

    </vxe-modal>
  </div>
</template>

<script>
var paramsModule = {
  customer_id: "",
  customer_name: "",
  goods_id: "",
};
export default {
  data() {
    return {
      showData: {},
      params: this.$publicFution.deepCopy(paramsModule),
      currentScope: "",
      tableColumns: [
        {
          title: "",
          type: "index",
          width: 65,
        },
        {
          title: "",
          field: "business_time",
          width: 100,
        },
        {
          title: "",
          field: "outstock_sn",
        },
        {
          title: "",
          field: "customer_name",
        },
        {
          title: "",
          field: "real_num",
          headerAlign: "right",
          align: "right",
          width: 70,
        },
        {
          title: "",
          field: "unit_name",
          width: 70,
        },
        {
          title: "",
          field: "unit_price",
          headerAlign: "right",
          align: "right",
          width: 70,
        },
      ],
    };
  },
  props: {
    value: null,
    company_id: "",
    row: {
      type: Object,
      default: () => {},
    },
    searchParams: {
      type: Object,
      default: () => {},
    },
  },
  methods: {
    dealData({ data }) {
      this.showData = data.data;
    },
    currentChange(scope) {
      this.currentScope = scope;
    },
    close() {
      this.$emit("input", false);
    },
    onReset() {
      this.params = this.$publicFution.deepCopy(paramsModule);
      this.params = Object.assign(this.params, this.searchParams);
      this.onSearch();
    },
    onSearch() {
      this.$refs.xTable._getData(1);
    },
    submit(scope) {
      var row = "";
      if (scope) {
        row = scope.row;
      } else {
        row = this.currentScope.row;
        if (!row) {
          return;
        }
      }
      this.$emit("sumbitData", row);
      this.close();
    },
  },
  created() {
    this.params = Object.assign(this.params, this.searchParams);
  },
};
</script>

